#drum-machine {
  margin-top: 10%;
  text-align: center;
}

.text {
  display: flex;
  justify-content: flex-start;
}

.display-container {
  width: 100%;
  display: flex;
  justify-content: center;
  background: #ffe5d9;
  padding: 20px;
}

#display-pads {
  display: flex;
  float: left;
  flex-flow: row wrap;
  justify-content: space-around;
  width: 320px;
  height: 320px;
  margin-top: 1%;
}

.display-container-right {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: 7%;
}

#volume-slider {
  width: 200px;
  margin-top: 30px;
  text-align: center;
}

input[type="range"] {
  -webkit-appearance: none;
  appearance: none;
  background: transparent;
  cursor: pointer;
  width: 12rem;
}

input[type="range"]::-webkit-slider-runnable-track {
  background-color: #d1b3c4;
  border-radius: 0.5rem;
  height: 0.5rem;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  margin-top: -12px;
  background-color: #5cd5eb;
  height: 2rem;
  width: 1rem;
  border-radius: 5px;
}

#display {
  width: 180px;
  height: 35px;
  border: solid 4px #d1b3c4;
  background-color: #fff;
  color: #000;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
}

@media screen and (max-width: 600px) {
  .display-container {
    flex-direction: column-reverse;
    align-items: center;
  }

  .display-container-right {
    margin-left: initial;
    margin-bottom: 20px;
  }
}